import React from 'react';
import styled from 'styled-components';
import { graphql, PageProps } from 'gatsby';
import Card from '@components/Card';
import { defineCustomElements as deckDeckGoElement } from '@deckdeckgo/highlight-code/dist/loader';

deckDeckGoElement();

import 'katex/dist/katex.min.css';

import {
  PostLayout,
  PostFileContent,
  PostMarkdownContent,
} from '../templates/post.styles';

interface AboutPageProps {
  file: {
    children: Array<{
      html: string;
    }>;
  } | null;
}

function About({
  data,
  pageContext,
}: PageProps<AboutPageProps, PageContextType>) {
  console.log('about page', pageContext);

  return (
    <ContentBox>
      <Card maxWidth="33%">
        <img
          style={{
            width: '100%',
            aspectRatio: '1/1',
            padding: '1.25rem',
          }}
          src={pageContext.profileUrl}
        />
      </Card>
      <Card title="소개">
        {data.file && (
          <PostLayout>
            <PostFileContent>
              <PostMarkdownContent>
                <div
                  style={{
                    padding: '1.25rem',
                  }}
                  dangerouslySetInnerHTML={{
                    __html: data.file.children[0].html,
                  }}
                />
              </PostMarkdownContent>
            </PostFileContent>
          </PostLayout>
        )}
      </Card>
    </ContentBox>
  );
}

export default About;

export const pageQuery = graphql`
  query AboutQuery {
    file(relativeDirectory: { eq: "" }, ext: { eq: ".md" }) {
      children {
        ... on MarkdownRemark {
          html
        }
      }
    }
  }
`;

export const ContentBox = styled.ul`
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  padding: 0;
`;
